
          @import "@/assets/themes/AppAddict/variables";
          @import "@/assets/core";
        

.purchase {
  text-align: center;
}

.icon {
  @include icon-shape;
  width: 7rem;
  height: 7rem;
}

.heading {
  font-size: 1.4rem;
  font-weight: 500;
  margin: 1rem 0;
}

.text {
  line-height: 1.2;
  margin: 1rem 0;

  &_margin-sm {
    margin: 0.5rem 0;
  }

  &_warning {
    .text__icon {
      color: var(--text-warning);
    }
  }

  &_success {
    .text__icon {
      color: var(--text-success);
    }
  }

  &__icon {
    width: 1.3em;
    height: 1.3em;
    vertical-align: bottom;
    position: relative;
    bottom: -0.05em;
    margin-right: 0.3em;
  }

  &::v-deep {
    b {
      font-weight: 500;
    }
  }
}

.agreement {
  margin: 0.75rem 0 1rem 0;
  justify-content: center;
  text-align: left;

  &__button {
    color: var(--primary);
    text-decoration: underline;
  }
}

.controls {
  display: grid;
  grid-template-columns: max-content;
  justify-content: center;
  gap: 0.5rem;
  margin: 1rem auto 0;
}

.tos-text {
  text-align: left;
  white-space: pre-line;
}

button {
  font-size: inherit;
}
